// @flow
import React, { Component } from 'react';


export default class Header extends Component {
    relative() {
        return this.props.relative ? 'relative' : '';
    }
    render() {
        return (
            <aside className={`main-sidebar ${this.relative()}`}>
                <section className="sidebar" style={{ height: 'auto' }}>
                    {this.props.children}
                </section>
            </aside>
        );
    }
}
